import React from "react";
import PropTypes from "prop-types";
import style from "./NewDropDown.module.css";
import Icon from "@material-ui/core/Icon";

export default class NewDropDown extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOption: "props.selectedOption",
      isOpen: false,
      arrow: "keyboard_arrow_down"
    };
    this.handleChange = this.handleChange.bind(this);
    this.toggleDropdown = this.toggleDropdown.bind(this);
  }

  componentDidMount() {
    var modal = document.getElementById("selectDropdown");
    var modal_inner = document.getElementsByClassName("item1");
    window.onclick = event => {
      if (
        event.target != modal &&
        event.target != modal_inner[0] &&
        event.target != modal_inner[1] &&
        event.target != modal_inner[2] &&
        event.target != modal_inner[3]
      ) {
        this.setState({
          isOpen: false,
          arrow: "keyboard_arrow_down"
        });
      }
    };
  }

  handleChange(val) {
    let { onChange } = this.props;
    let arrow =
      this.state.arrow === "keyboard_arrow_down"
        ? "keyboard_arrow_up"
        : "keyboard_arrow_down";
    this.setState({ selectedOption: val, arrow });
    onChange && onChange(val);
  }

  toggleDropdown() {
    let arrow =
      this.state.arrow === "keyboard_arrow_down"
        ? "keyboard_arrow_up"
        : "keyboard_arrow_down";
    this.setState({
      isOpen: !this.state.isOpen,
      arrow
    });
  }

  render() {
    let { options, disabled = false } = this.props;
    const { isOpen, selectedOption = "", arrow } = this.state;
    let cname = "item1";
    let selectedLabel = null;
    let html = options.map((obj, i) => {
      if (selectedOption === obj.value && selectedLabel === null) {
        selectedLabel = obj.label;
      }
      return (
        <span
          key={i}
          className={style.dropdownList}
          onClick={this.handleChange.bind(this, obj.value)}
        >
          {obj.label}
        </span>
      );
    });
    return (
      <div className={style.selectContainer}>
        <div
          id="selectDropdown"
          className={
            isOpen === true ? style.selectStyle_click : style.selectStyle
          }
          onClick={this.toggleDropdown}
        >
          <div className="item1">
            <span className={`${style.selectedTxt} ${cname}`}>
              {selectedLabel || "Choose Option"}
            </span>
          </div>
          <div className="item1">
            <Icon className={`${style.item2}  item1`}>{arrow}</Icon>
          </div>
        </div>
        {isOpen ? <div className={style.selectDropdown}>{html}</div> : null}
      </div>
    );
  }
}

NewDropDown.defaultProps = {
  options: [
    { value: "chocolate", label: "Chocolate" },
    { value: "strawberry", label: "Strawberry" },
    { value: "vanilla", label: "Vanilla" }
  ],
  disabled: false,
  dataId: "dropdownComp",
  selectedOption: "chocolate"
};
NewDropDown.propTypes = {
  dataId: PropTypes.string,
  disabled: PropTypes.bool,
  onChange: PropTypes.func,
  options: PropTypes.array,
  selectedOption: PropTypes.string
};
