import React from "react";
import PropTypes from "prop-types";
import style from "./Button.module.css";

export default class Button extends React.Component {
  render() {
    let { text, size, onClick, type, dataId, children } = this.props;
    let icon = size === "medium" ? "icon" : "icon-" + size;
    type = children === undefined ? type : "primaryicon";
    return (
      <div onClick={onClick} className={style["input-icons"]}>
        <i
          className={
            children === undefined ? style["display-none"] : style[icon]
          }
        >
          {children}
        </i>
        <button
          className={`${style[type.toLowerCase()]} ${
            style[size.toLowerCase()]
          } `}
          data-id={dataId}
          type="button"
        >
          {text}
        </button>
      </div>
    );
  }
}

Button.defaultProps = {
  size: "medium",
  text: "Button",
  onClick: () => {
    console.log("clicked");
  },
  dataId: "buttonComp",
  type: "primary"
};
Button.propTypes = {
  dataId: PropTypes.string,
  onClick: PropTypes.func,
  type: PropTypes.oneOf(["primary", "secondary", "disabled"]),
  size: PropTypes.oneOf(["small", "medium", "large"]),
  text: PropTypes.string
};
