import React, { Component } from "react";
import PropTypes from "prop-types";
import style from "./DropList.module.css";

class DropList extends Component {
  state = { isOpen: false, selectedOption: {} };
  componentDidMount() {
    const modal = document.getElementById("menuButton");
    window.onclick = event => {
      if (event.target != modal) {
        this.setState({ isOpen: false });
      }
    };
  }
  handleClick = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };
  handleSelect = data => {
    const selectedOption = {};
    selectedOption["label"] = data.label;
    selectedOption["value"] = data.value;
    this.setState({ selectedOption });
  };
  render() {
    const { isOpen, selectedOption } = this.state;
    const { options } = this.props;
    console.log(selectedOption);
    return (
      <React.Fragment>
        <div
          id="menuButton"
          className={style["parent"]}
          onClick={this.handleClick}
        >
          ...
        </div>
        <div className={`${style.menu}`}>
          {isOpen &&
            options.map(data => (
              <span
                className={style.menuitems}
                key={data.value}
                value="as"
                onClick={() => this.handleSelect(data)}
              >
                {data.label}
              </span>
            ))}
        </div>
      </React.Fragment>
    );
  }
}

DropList.defaultProps = {
  options: [
    { value: "sample1", label: "SAMPLE-1" },
    { value: "sample2", label: "SAMPLE-2" },
    { value: "sample3", label: "SAMPLE-3" }
  ],
  dataId: "dropdownComp"
};
DropList.propTypes = {
  options: PropTypes.arrayOf({
    value: PropTypes.string,
    label: PropTypes.number
  }),
  handleClick: PropTypes.func,
  handleSelect: PropTypes.func
};
export default DropList;
